body,
html,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: 'Public Sans', sans-serif;
  font-size: 10pt;
  color: white;
}

::-webkit-scrollbar {
  display: none;
}

* {
  -webkit-overflow-scrolling: touch;
}

a {
  font-weight: bold;
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.container {
  width: 100%;
  background: black;
}

.content {
  margin: 0 auto;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.content-scrollable {
  overflow: scroll;
  height: 100vh;
  width: 100vw;
  position: relative;
  right: calc(var(--mouse-x) * 8px);
  top: calc(var(--mouse-y) * -8px);
  -webkit-transition: all ease-out 0.2s;
  -o-transition: all ease-out 0.2s;
  transition: all ease-out 0.2s;
  padding: 160px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.left-bar {
  position: absolute;
  left: calc(var(--mouse-x) * -24px + 50% - 440px);
  -webkit-transform: rotate(-90deg) translateY(-50%);
      -ms-transform: rotate(-90deg) translateY(-50%);
          transform: rotate(-90deg) translateY(-50%);
  top: calc(var(--mouse-y) * -16px + 50%);
  letter-spacing: 3px;
  -webkit-transition: all ease 0.2s;
  -o-transition: all ease 0.2s;
  transition: all ease 0.2s;
}

nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

nav > a {
  margin: 8px;
}

.right-bar {
  position: absolute;
  right: calc(var(--mouse-x) * 24px + 50% - 360px);
  -webkit-transform: rotate(90deg) translateY(-50%);
      -ms-transform: rotate(90deg) translateY(-50%);
          transform: rotate(90deg) translateY(-50%);
  top: calc(var(--mouse-y) * -16px + 50%);
  letter-spacing: 5px;
  text-transform: uppercase;
  -webkit-transition: all ease-out 0.2s;
  -o-transition: all ease-out 0.2s;
  transition: all ease-out 0.2s;
}

.bottom-bar {
  position: absolute;
  bottom: calc(var(--mouse-y) * 24px + 10px);
  right: calc(var(--mouse-x) * 16px + 40%);
  font-size: 12px;
  letter-spacing: 2px;
  -webkit-transition: all ease-out 0.2s;
  -o-transition: all ease-out 0.2s;
  transition: all ease-out 0.2s;
}
.top-bar {
  position: absolute;
  top: calc(var(--mouse-y) * -16px + 100px);
  left: calc(var(--mouse-x) * -24px + 40%);
  -webkit-transition: all ease-out 0.2s;
  -o-transition: all ease-out 0.2s;
  transition: all ease-out 0.2s;
}

.landing {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
}
.landing img {
  width: 460px;
  /*height: 360px;*/
  -o-object-fit: cover;
     object-fit: cover;
  margin-bottom: 24px;
  -ms-flex-item-align: center;
      align-self: center;
}
.landing iframe {
  -ms-flex-item-align: center;
      align-self: center;
}

.landing h3 {
  font-weight: 800;
  letter-spacing: 5px;
  font-size: 96px;
  color: #ff3500;
  position: absolute;
  top: 50px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  white-space: nowrap;
  text-transform: uppercase;
}

.landing h3::before {
  content: attr(data-text);
  position: absolute;
  top: -35%;
  -webkit-clip-path: inset(0 0 52% 0);
          clip-path: inset(0 0 52% 0);
  text-transform: uppercase;
}

.landing h3.bottom {
  bottom: 20%;
  top: initial;
}

.landing h3.bottom::before {
  bottom: -25%;
  top: initial;
  -webkit-clip-path: inset(65% 0 0 0);
          clip-path: inset(65% 0 0 0);
}

.landing .cover {
  position: relative;
}

.print {
  margin: 0 auto;
  width: 500px;
}
.print .gallery {
  margin-bottom: 48px;
}
.print .gallery:nth-last-child(2) {
  margin-bottom: 0;
}

.about {
  width: 500px;
  margin: 0 auto;
}

@media only screen and (max-width: 768px) {
  /* responsive here */
  .content {
    height: auto;
  }
  .content-anchor {
    width: auto;
    height: auto;
  }

  .content-scrollable {
    padding: 84px 60px;
    margin: 0;
  }

  .top-bar,
  .left-bar,
  .bottom-bar,
  .right-bar {
    position: fixed;
  }
  .top-bar {
    top: 10px;
  }
  .bottom-bar {
    bottom: 10px;
  }
  .left-bar {
    left: -100px;
  }
  .right-bar {
    right: 10px;
  }

  .landing h3 {
    font-size: 36px;
  }
  .landing iframe,
  .landing img {
    max-width: 100%;
  }

  .about,
  .print {
    max-width: 100%;
  }
}



